<template>
  <div class="about">
    <nav-bar></nav-bar>
    <div id="aboutContent" class="about-content">
      <div class="about-detail">
        <h1>ABOUT ME</h1>
        <p>A Full Stack Developer who specializes in JavaScript Technologies across the whole stack (React, Vue, Angular, Node.js, Express, MySQL, MongoDB, AWS). 
          Experience building complete web applications with backend API Systems.</p>
        <!-- <p>I’m a hard-working, patient, organized, detail-oriented and multitasking worker looking to apply skills to the Web Developing career.</p> -->
      </div>
      <div @click="moveSkill" class="skill-tool">
        Check my skills!
      </div>
    </div>
    <div id="skillContent" class="myskills-content">
      <h1>MY SKILLS</h1>
        <div class="circle-menu-a" v-bind:class="[skill1, skillpos.skill1]">
          <p>{{ skill1name }}</p>
        </div>
         <div class="circle-menu-a" v-bind:class="[skill2, skillpos.skill2]">
          <p>{{ skill2name }}</p>
        </div>
        <div class="circle-menu-a" v-bind:class="[skill3, skillpos.skill3]">
          <p>{{ skill3name }}</p>
        </div>
        <div class="circle-menu-a" v-bind:class="[skill4, skillpos.skill4]">
          <p>{{ skill4name }}</p>
        </div>
        <div class="circle-menu-a" v-bind:class="[skill5, skillpos.skill5]">
          <p>{{ skill5name }}</p>
        </div>
        <div class="circle-menu-a" v-bind:class="[skill6, skillpos.skill6]">
          <p>{{ skill6name }}</p>
        </div>
        <div class="circle-menu-a" v-bind:class="[skill7, skillpos.skill7]">
          <p>{{ skill7name }}</p>
        </div>
        <!-- <div @click="changeRouter('contact')" class="circle-menu-a hireme" v-bind:class="[skill8]">
          <p>Hire me!</p>
        </div> -->
    </div>
  </div>
</template>

<script>
import navbar from './navbar.vue'

var scrolled = false

export default {
  mounted () {
    window.scrollTo(0, 0)
    this.$parent.pageLoaded()
    if (this.$isMobile()) {
      this.startSkillsAnimation()
    }
  },
  data () {
    return {
      actualContent: 0,
      arraySkills: ['Unity3D', 'ReactJS', 'MongoDB', 'MySQL', 'SASS', 'PHP', 'NodeJS'],
      arrayPotitions: ['pos1', 'pos2', 'pos3', 'pos4', 'pos5', 'pos6', 'pos7'],
      skill1: '',
      skill2: '',
      skill3: '',
      skill4: '',
      skill5: '',
      skill6: '',
      skill7: '',
      skill8: '',
      skill1name: '',
      skill2name: '',
      skill3name: '',
      skill4name: '',
      skill5name: '',
      skill6name: '',
      skill7name: '',
      skillpos: [
        {
          skill1: '',
          skill2: '',
          skill3: '',
          skill4: '',
          skill5: '',
          skill6: '',
          skill7: ''
        }
      ]
    }
  },
  components: {
    'nav-bar': navbar
  },
  methods: {
    changeRouter: function (page) {
      this.$parent.changePage(page)
    },
    handleScroll: function (e) {
      if (!scrolled && !this.$isMobile()) {
        e = window.event || e
        var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)))
        if (delta < 0 && this.actualContent === 0) {
          this.$SmoothScroll(document.getElementById('skillContent'), 1500, () => {
            scrolled = false
            this.startSkillsAnimation()
            // setInterval(this.startSkillsAnimation, 5000)
            // this.homeStart = true
          }, undefined)
          scrolled = true
          this.actualContent = 1
        } else {
          if (delta > 0 && this.actualContent === 1) {
            this.$SmoothScroll(document.getElementById('aboutContent'), 1500, () => {
              scrolled = false
              // this.homeStart = true
            }, undefined)
            scrolled = true
            this.actualContent = 0
          }
        }
      }
      e.preventDefault()
    },
    moveSkill: function () {
      this.$SmoothScroll(document.getElementById('skillContent'), 1500, () => {
        scrolled = false
        this.startSkillsAnimation()
      }, undefined)
      scrolled = true
      this.actualContent = 1
    },
    startSkillsAnimation: function () {
      this.arraySkills.sort(function (a, b) { return 0.5 - Math.random() })
      this.arrayPotitions.sort(function (a, b) { return 0.5 - Math.random() })
      this.skill1name = this.arraySkills[0]
      this.skill2name = this.arraySkills[1]
      this.skill3name = this.arraySkills[2]
      this.skill4name = this.arraySkills[3]
      this.skill5name = this.arraySkills[4]
      this.skill6name = this.arraySkills[5]
      this.skill7name = this.arraySkills[6]
      this.skillpos.skill1 = this.arrayPotitions[0]
      this.skillpos.skill2 = this.arrayPotitions[1]
      this.skillpos.skill3 = this.arrayPotitions[2]
      this.skillpos.skill4 = this.arrayPotitions[3]
      this.skillpos.skill5 = this.arrayPotitions[4]
      this.skillpos.skill6 = this.arrayPotitions[5]
      this.skillpos.skill7 = this.arrayPotitions[6]
      this.skill1 = 'go'
      setTimeout(() => { this.skill2 = 'go' }, 400)
      setTimeout(() => { this.skill3 = 'go' }, 800)
      setTimeout(() => { this.skill4 = 'go' }, 1200)
      setTimeout(() => { this.skill5 = 'go' }, 1600)
      setTimeout(() => { this.skill6 = 'go' }, 2000)
      setTimeout(() => { this.skill7 = 'go' }, 2400)
      setTimeout(() => { this.skill8 = 'go' }, 2800)
    }
  },
  created () {
    window.addEventListener('wheel', this.handleScroll, false)
  },
  destroyed () {
    window.removeEventListener('wheel', this.handleScroll)
  }
}
</script>

<style lang="scss" scoped>

@import '../styles/main.scss';

.about{
  background-image: url('../assets/bg3.jpg');
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
}

.about-content{
  position: relative;
  width: 100%;
  height: 100vh;
  padding-left: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  @include respond-to(tabmob){
    padding: 0px;
  }

  h1{
    width: 100%;
    text-align: center;
  }
  .about-detail{
    padding: 15px 30px 25px 30px;
    color: white !important;
    background-color: #214759;
    width: 50%;
    box-shadow: 0px 7px 30px 10px rgba(0,0,0,0.3);
    @include respond-to(tabmob){
      width: 98%;
    }
  }

  .skill-tool{
    border: 3px solid #214759;
    padding: 4px 7px;
    position: absolute;
    bottom: 50px;
    color: #214759;
    cursor: pointer;
    transition: all 0.6s ease;
    &:hover{
      transform: scale(1.2);
    }
  }
}

.myskills-content{
  position: relative;
  width: 100%;
  h1{
    color: black;
    width: 100%;
    text-align: center;
  }
  p{
    color: black;
  }
  @include respond-to(desktop){
    height: 100vh;
    padding-left: 70px;
  }
  @include respond-to(tablet){
    height: 100vh;
  }
  @include respond-to(mobile){
    padding-top: 80px;
    padding-bottom: 20px;
  }
}

.circle-menu-a{
  position:absolute;
  background:#214759;
  @include respond-to(destab){
    min-width: 100px;
    min-height: 100px;
    width:10vw;
    height:10vw;
    border-radius:50%;
  }
  @include respond-to(mobile){
    margin: 5px 10px;
  }
  display:flex;
  align-items:center;
  justify-content: center;
  opacity:0;
  transition: all 2s cubic-bezier(0.39, 0.575, 0.565, 1);
  p{
    color: white;
  }
}

.circle-menu-a.go{
  animation: elastic 1.25s 0s ease-in-out none;
  opacity: 1;
}

.pos1{
  @include respond-to(destab){
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos2{
  @include respond-to(destab){
    position: absolute;
    top: 30%;
    left: 60%;
    transform: translate(-50%,-50%);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos3{
  @include respond-to(destab){
    position: absolute;
    top: 80%;
    left: 80%;
    transform: translate(-50%,-50%);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos4{
  @include respond-to(destab){
    position: absolute;
    top: 45%;
    right: 10%;
    transform: translate(-50%,-50%) scale(1.5);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos5{
  @include respond-to(destab){
    position: absolute;
    top: 30%;
    left: 20%;
    transform: translate(-50%,-50%);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos6{
  @include respond-to(destab){
    position: absolute;
    top: 70%;
    left: 30%;
    transform: translate(-50%,-50%) scale(1.8);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos8{
  @include respond-to(destab){
    position: absolute;
    top: 80%;
    left: 60%;
    transform: translate(-50%,-50%)  scale(1.3);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.pos7{
  @include respond-to(destab){
    position: absolute;
    top: 35%;
    left: 35%;
    transform: translate(-50%,-50%) scale(0.9);
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

.hireme{
  @include respond-to(destab){
    cursor: pointer;
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,-50%) scale(1.2);
    animation: hiremeAni 1.4s ease-in-out infinite !important;
  }
  @include respond-to(mobile){
    position: relative;
    transform: translate(0) scale(1);
  }
}

@keyframes hiremeAni {
  0% {
		transform: scale(1.2);
	}
	50% {
		transform: scale(1.4);
	}
  100% {
		transform: scale(1.2);
	}
}


@keyframes elastic {
	0% {
		transform: scale(0);
    opacity:0;
	}
	20% {
		transform: scale(1.2);
    opacity:1;
	}
	40% {
		transform: scale(.8);
    opacity:1;
	}
	60% {
		transform: rotateX(1.1);
    opacity:1;
	}
  80% {
		transform: rotateX(.8);
    opacity:1;
	}
	100% {
		transform: rotateX(1);
    opacity:1;
	}
}




</style>



