            </section>
          </div>
        </div>
      </section>
      <!-- <a class="link-btn lwork5" href="http://yukionlineapp.com/" target="_blank">View Website</a> -->
    </div>
    <div class="works-content work4" v-bind:class="{ go: work4 }">
      <h1>YOSHI'S HAIR STUDIO</h1>
      <section class="container-flip">
        <div v-bind:class="'card ' + [workdesc[1]]"  @click="flipContent(1)">
          <div class="front">
            <p>DEC 2016 - MAR 2017</p>
            <p>Web Developer/Designer - Vanarts Institute of Media Arts </p>
            <h3>YOSHI'S HAIR STUDIO</h3>
