              <i class="devicon-php-plain"></i>
              <i class="devicon-mysql-plain"></i>
            </section>
          </div>
        </div>
      </section>
      <!-- <a class="link-btn lwork4" href="http://yoshihair.yukionlineapp.com/" target="_blank">View Website</a> -->
    </div>
    <div class="works-content work3" v-bind:class="{ go: work3 }">
      <h1>AHMED HINDAWY</h1>
      <section class="container-flip">
        <div v-bind:class="'card ' + [workdesc[0]]"  @click="flipContent(0)">
          <div class="front">
            <p>SEP 2016 - NOV 2016</p>
            <p>Web Developer/Designer - Vanarts Institute of Media Arts</p>
            <h3>AHMED HINDAWY PORTFOLIO</h3>
            <p>Project Management</p>
            <p>Design</p>
          </div>
          <div class="back">
            <h3>TEGHNOLOGIES</h3>
            <section class="tech-icons">
              <i class="devicon-sass-original"></i>
              <i class="devicon-angularjs-plain"></i>
              <i class="devicon-php-plain"></i>
              <i class="devicon-mysql-plain"></i>
