          </div>
        </div>
      </section>
      <!-- <a class="link-btn lwork3" href="http://ahmedhindawy.yukionlineapp.com/" target="_blank">View Website</a> -->
    </div>
    <div class="works-content work2" v-bind:class="{ go: work2 }">
      <h1>PLAYBALL APP</h1>
      <section class="container-flip flipmob">
        <div v-bind:class="'card ' + [workdesc[3]]"  @click="flipContent(3)">
          <div class="front">
            <p>JUL 2017 - AUG 2017</p>
            <p>Web Developer/Designer - Vanarts Institute of Media Arts </p>
