    <div class="works-content work1" v-bind:class="{ go: work1 }">
      <h1>SATOSHI PORTFOLIO</h1>
      <section class="container-flip">
        <div v-bind:class="'card ' + [workdesc[4]]"  @click="flipContent(4)">
          <div class="front">
            <p>AUG 2017 - WORKING</p>
            <!-- <p>Web Developer/Designer - Vanarts Institute of Media Arts </p> -->
            <h3>SATOSHI PORTFOLIO</h3>
