    </div>
    <div class="works-bar">
      <div @click="showSPage(1)" v-bind:class="{'works-circle ':true , 'active': (actualPage == 0) }">
        <div>
          SATOSHI
        </div>
      </div>
      <div @click="showSPage(2)" v-bind:class="{'works-circle ':true , 'active': (actualPage == 1) }">
        <div>
          PLAYBALL APP
        </div>
      </div>
      <div @click="showSPage(3)" v-bind:class="{'works-circle ':true , 'active': (actualPage == 2) }">
        <div>
          AHMED HINDAWY
        </div>
      </div>
      <div @click="showSPage(4)" v-bind:class="{'works-circle ':true , 'active': (actualPage == 3) }">
        <div>
          YOSHI'S HAIR STUDIO
        </div>
      </div>
      <div @click="showSPage(5)" v-bind:class="{'works-circle ':true , 'active': (actualPage == 4) }">
        <div>
          THINK W
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import navbar from './navbar.vue'

var scrolled = false
// var workData = [{
//   title: '',
//   date: '',
// }, {

// }];

export default {
  mounted () {
    this.$parent.pageLoaded()
  },
  data () {
    return {
      actualPage: 0,
      work1: false,
      work2: false,
      work3: false,
      work4: false,
      work5: false,
      workdesc: ['', '', '', '', '']
    }
  },
  components: {
    'nav-bar': navbar
  },
  methods: {
    changeRouter: function (page) {
      this.$parent.changePage(page)
    },
    handleScroll: function (e) {
      if (!scrolled) {
        e = window.event || e
        var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)))
        if (delta < 0 && this.actualPage < 4) {
          this.actualPage += 1
          if (this.actualPage === 1) {
            this.work1 = true
          } else if (this.actualPage === 2) {
            this.work2 = true
          } else if (this.actualPage === 3) {
            this.work3 = true
          } else if (this.actualPage === 4) {
            this.work4 = true
          }

          setTimeout(() => {
            scrolled = false
          }, 1500)
          scrolled = true
        } else if (delta > 0 && this.actualPage > 0) {
          this.actualPage -= 1
          if (this.actualPage === 0) {
            this.work1 = false
            this.work2 = false
            this.work3 = false
            this.work4 = false
            this.work5 = false
          } else if (this.actualPage === 1) {
            this.work1 = true
            this.work2 = false
            this.work3 = false
            this.work4 = false
            this.work5 = false
          } else if (this.actualPage === 2) {
            this.work1 = true
            this.work2 = true
            this.work3 = false
            this.work4 = false
            this.work5 = false
          } else if (this.actualPage === 3) {
            this.work1 = true
            this.work2 = true
            this.work3 = true
            this.work4 = false
            this.work5 = false
          } else if (this.actualPage === 4) {
            this.work1 = true
            this.work2 = true
            this.work3 = true
            this.work4 = true
            this.work5 = false
          }

          setTimeout(() => {
            scrolled = false
          }, 1500)
          scrolled = true
        }
      }
      e.preventDefault()
    },
    showSPage: function (page) {
      if (page === 1) {
        this.work1 = false
        this.work2 = false
        this.work3 = false
        this.work4 = false
        this.work5 = false
      } else if (page === 2) {
        this.work1 = true
        this.work2 = false
        this.work3 = false
        this.work4 = false
        this.work5 = false
      } else if (page === 3) {
        this.work1 = true
        this.work2 = true
        this.work3 = false
        this.work4 = false
        this.work5 = false
      } else if (page === 4) {
        this.work1 = true
        this.work2 = true
        this.work3 = true
        this.work4 = false
        this.work5 = false
      } else if (page === 5) {
        this.work1 = true
        this.work2 = true
        this.work3 = true
        this.work4 = true
        this.work5 = false
      }
      this.actualPage = page - 1
    },
    flipContent: function (divs) {
      let arrayWork = []
      for (var i = 0; i < this.workdesc.length; i++) {
        arrayWork.push('')
      }

      if (this.workdesc[divs] === '') {
        arrayWork[divs] = 'flip'
      }

      this.workdesc = arrayWork
      // .toggleClass('flipped');
    }
  },
  created () {
    window.addEventListener('wheel', this.handleScroll, false)
  },
  destroyed () {
    window.removeEventListener('wheel', this.handleScroll)
  }
}
</script>

<style lang="scss" scoped>
// @import '../styles/main.scss';
</style>



