<template>
  <div>
    <div v-on:mouseover="menuHover" v-on:mouseout="menuHoverOut" class="nav-style">
      <div class="logo-menu"><img @click="changeRouter('')" class="logo" src="../assets/logofinal.svg"></div>
      <div class="menu-bar">
        <div @click="changeRouter('about')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconUser" alt="about icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">ABOUT</span></div>
        <div @click="changeRouter('works')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconWorks" alt="works icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">WORKS</span></div>
        <div @click="changeRouter('contact')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconContact" alt="contact icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">CONTACT</span></div>
      </div>
      <div class="social-menu">
        <a target="_blank" href="https://www.linkedin.com/in/yuki-ohori-89248a13b/"><i class="fa fa-linkedin" aria-hidden="true"></i></a>
        <a target="_blank" href="https://github.com/yukiohori"><i class="fa fa-github" aria-hidden="true"></i></a>
        <a @click="changeRouter('contact')"><i class="fa fa-envelope" aria-hidden="true"></i></a>
      </div>
    </div>
    <div @click="showMenuMobile()" class="nav-style-mobile " v-bind:class="{ 'hide-menu-mobile' :mobileMenu }">
      <div class="menu-mobile">
        <div class="logo-menu"><img class="logo" src="../assets/logofinal.svg"></div>
        <div class="menu-bar">
          <div @click="changeRouter('about')"><img :src="iconUser" alt="about icon" /><span>ABOUT</span></div>
          <div @click="changeRouter('works')"><img :src="iconWorks" alt="works icon" /><span>WORKS</span></div>
          <div @click="changeRouter('contact')"><img :src="iconContact" alt="contact icon" /><span>CONTACT</span></div>
        </div>
        <div class="menu-social">
          <a target="_blank" href="https://www.linkedin.com/in/yuki-ohori-89248a13b/"><i class="fa fa-linkedin" aria-hidden="true"></i></a>
          <a target="_blank" href="https://github.com/yukiohori"><i class="fa fa-github" aria-hidden="true"></i></a>
          <a @click="changeRouter('contact')"><i class="fa fa-envelope" aria-hidden="true"></i></a>
        </div>
        <div class="close-menu">CLOSE</div>
      </div>
    </div>
  </div>
</template>

<script>
import iconabout from '../assets/man-user.png'
import iconworks from '../assets/programming.png'
import iconcontact from '../assets/contact.png'

export default {
  data () {
    return {
      iconUser: iconabout,
      iconWorks: iconworks,
      iconContact: iconcontact,
      hovermenu: false,
      mobileMenu: true
    }
  },
  methods: {
    changeRouter (page) {
      this.$parent.changeRouter(page)
    },
    menuHover: function () {
      this.hovermenu = true
    },
    menuHoverOut: function () {
      this.hovermenu = false
    },
    showMenuMobile: function () {
      this.mobileMenu = !this.mobileMenu
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../styles/main.scss';

.nav-style{
  z-index: 900;
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  left:0px;
  top:0;
  width: 70px;
  height: 100vh;
  background-color: #214759;
  transition: all 0.5s ease;

  @include respond-to(tabmob){
    display: none;
  }

  img{
    width: 70%;
    max-width: 21px;
  }
  .menu-bar{
    width: 100%;
    display: flex;
    align-self: center;
    flex-direction: column;
    text-align: center;
    div{
      position: relative;
      margin: 40px 0;
      cursor: pointer;
    }
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      transition: all 0.5s ease;
    }
    .menuhide-span{
      font-size: 13px;
      margin: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transition: all 0.5s ease;
      transform: translate(-50%,-50%);
    }
  }
  &:hover{
    width: 80px;
  }
  .logo-menu{
    margin-top: 20px;
    width: 100%;
    align-self: flex-start;
    text-align: center;
    cursor: pointer;
  }
  .social-menu{
    width: 100%;
    align-self: flex-end;
    text-align: center;
    display: flex;
    flex-direction: column;
    i{
      margin-bottom: 10px;
    }
  }

  div{
    color: white;
  }

  .hide-menuimg{
    opacity: 0;
  }
}

.menu-social {
  @include respond-to(tabmob){
    margin-top: 100px;
  }
  i {
    text-align: center;
    width: 50px;
  }
}


.nav-style-mobile{
  @include respond-to(tabmob){
    z-index: 900;
    position: fixed;
    left:0px;
    top:0;
    width: 100vw;
    height: 100%;
    background-color: #214759;
    transition: all 0.5s ease;
    overflow: hidden;
  }

  .menu-mobile{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    color: white;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  @include respond-to(desktop){
    display: none;
  }

  img{
    margin-right: 30px;
    margin-bottom: 20px;
  }

  .logo-menu{
    top: 0;
    width: 70px;
    position: absolute;
    @include respond-to(desktop){
      display: none;
    }
  }

  .close-menu{
    text-align: center;
    width: 100px;
    bottom: 10px;
    position: absolute;
  }

  &.hide-menu-mobile{
    height: 50px;
  }
}

</style>



