<template>
  <div class="loading-style" v-bind:class="{ go: loadingState }">
    <h1>LOADING...</h1>
  </div>
</template>

<script>

export default {
  props: {
    loadingState: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="scss" scoped>

@import '../styles/main.scss';

.loading-style{
  top: 50%;
  left: 50%;
  position: fixed;
  transform: scale(0) translate(-50%,-50%);
  transform-origin: 0% 0%;
  transition: all 1s ease;
  @include respond-to(desktop) {
    height: 120vw;
    width: 120vw;
  }
  @include respond-to(tabmob) {
    height: 200vw;
    width: 200vw;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2d576b;
  z-index: 999;
  border-radius: 50%;
  h1{
    color: white;
  }
}

.go {
  transform: scale(1.5) translate(-50%,-50%);
}
</style>



