<template>
  <div class="contact">
    <nav-bar></nav-bar>
    <div class="content-contact">
      <div class="contact-context">
        <!-- <h2>HIRE ME!</h2> -->
        <h3>Looking for a full stack web developer?</h3>
        <h3>You've come to the right place.</h3>
      </div>
      <div class="form-contact">
        <h2 class="text-center">CONTACT ME</h2>
        <form v-on:submit.prevent="sendContact">
          <div class="clearfix">
            <div class="small-12 medium-6 large-6 column">
              <div>
                <input v-model="newContact.cname" type="text" placeholder="Your Name*" required>
              </div>
              <div>
                <input v-model="newContact.cemail" type="email"  placeholder="Your Email Address*" required>
              </div>
              <div>
                <textarea v-model="newContact.csubject" class="left-textarea" placeholder="Subject"></textarea>
              </div>
            </div>
            <div class="small-12 medium-6 large-6 column">
              <textarea v-model="newContact.cmessage" class="right-textarea" placeholder="Type your message here..."></textarea>
            </div>
          </div>
          <div class="clearfix">
            <div class="clearfix">
              <p class="text-center">{{alertMessage}}</p>
            </div>
            <div class="small-12 medium-12 large-12 column text-center">
              <input type="submit">
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import navbar from './navbar.vue'

export default {
  mounted () {
    this.$parent.pageLoaded()
  },
  data () {
    return {
      alertMessage: '',
      newContact: {
        cname: '',
        cemail: '',
        csubject: '',
        cmessage: ''
      }
    }
  },
  components: {
    'nav-bar': navbar
  },
  methods: {
    sendContact: function () {
      if (this.newContact.cname !== '' && this.newContact.cemail !== '') {
        this.newContact.cname = ''
        this.newContact.cemail = ''
        this.newContact.csubject = ''
        this.newContact.cmessage = ''
        this.alertMessage = 'Your Message has been sent! Thank you!'
      }
    },
    changeRouter: function (page) {
      this.$parent.changePage(page)
    }
  }
}
</script>

<style lang="scss" scoped>
// @import '../styles/main.scss';
.contact{
  background-image: url('../assets/bg3.jpg');
}
</style>



