<template>
  <div id="app">
    <my-loading :loadingState="loading"></my-loading>
    <!--<button @click="changePage">Demo</button>-->
    <router-view></router-view>
  </div>
</template>

<script>

import loading from './components/loading.vue'

export default {
  name: 'app',
  mounted () {
    // console.log(this.$route.path)
    this.lastPage = this.$route.path.replace('/', '')
  },
  data () {
    return {
      lastPage: '',
      loading: false
    }
  },
  methods: {
    changePage: function (page) {
      if (page !== this.lastPage) {
        this.lastPage = page
        this.loading = !this.loading
        setTimeout(() => {
          this.$router.push('/' + page)
        }, 1500)
      }
    },
    pageLoaded: function () {
      this.loading = false
    }
  },
  components: {
    'my-loading': loading
  }
}
</script>

<style lang="scss">
@import './styles/main.scss';

body, html {
   overflow-x: hidden;
}

</style>



