<template>
  <div class="home">
    <div class="header" v-show="showHeader">
      <div id="circle-animate" class="circle go">
        <img src="../assets/logofinal.svg" alt="scroll down icon">
      </div>
      <div class="circle-menu about" @click="changeRouter('about')" v-bind:class="{ go: aboutStart }">
        <a>ABOUT</a>
      </div>
      <div class="circle-menu works" @click="changeRouter('works')" v-bind:class="{ go: worksStart }">
        <a>WORKS</a>
      </div>
      <div class="circle-menu contact-h" @click="changeRouter('contact')"  v-bind:class="{ go: contactStart }">
        <a>CONTACT</a>
      </div>
      <div class="social-nav">
        <a target="_blank" href="https://www.linkedin.com/in/yuki-ohori-89248a13b/"><i class="fa fa-2x fa-linkedin" aria-hidden="true"></i></a>
        <a target="_blank" href="https://github.com/yukiohori"><i class="fa fa-2x fa-github" aria-hidden="true"></i></a>
        <a @click="changeRouter('contact')"><i class="fa fa-2x fa-envelope" aria-hidden="true"></i></a>
      </div>
      <div class="scroll-down" v-bind:class="{ go: contactStart }" @click="showHomeContent()">
        <img src="../assets/scrolldown.png" alt="scroll down icon">
      </div>
    </div>
    <div id="contentHome" class="home-content">
      <video @loadeddata="videoLoaded" ref="videobg" class="video-background" loop muted>
        <source :src="myVideo" type="video/mp4">
        Your browser does not support the video tag.
      </video>
      <div class="video-overlay"></div>
      <div v-on:mouseover="menuHover" v-on:mouseout="menuHoverOut" class="nav-style">
        <div class="logo-menu"><img @click="changeRouter('')" class="logo" src="../assets/logofinal.svg"></div>
        <div class="menu-bar">
          <div @click="changeRouter('about')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconUser" alt="about icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">ABOUT</span></div>
          <div @click="changeRouter('works')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconWorks" alt="works icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">WORKS</span></div>
          <div @click="changeRouter('contact')"><img v-bind:class="{ 'hide-menuimg':hovermenu }" :src="iconContact" alt="contact icon" /><span v-bind:class="{ 'menuhide-span':true,'hide-menuimg':!hovermenu }">CONTACT</span></div>
        </div>
        <div class="social-menu">
          <i class="fa fa-linkedin" aria-hidden="true"></i>
          <i class="fa fa-github" aria-hidden="true"></i>
          <i class="fa fa-envelope" aria-hidden="true"></i>
        </div>
      </div>
      <div class="menu-mobile">
        <div class="menu-bar">
          <div @click="changeRouter('about')"><img :src="iconUser" alt="about icon" /></div>
          <div @click="changeRouter('works')"><img :src="iconWorks" alt="works icon" /></div>
          <div @click="changeRouter('contact')"><img :src="iconContact" alt="contact icon" /></div>
        </div>
      </div>
      <div class="home-show" v-bind:class="{ go: homeStart }">
        <h1>Hi,</h1>
        <h1>I'M YUKI,</h1>
        <h1>FULL STACK WEB DEVELOPER.</h1>
      </div>
      <div class="home-image">
      </div>
    </div>
  </div>
</template>

<script>
import navbar from './navbar.vue'
import imageShow from '../assets/work1.png'
import videoShow from '../assets/bg-video.mp4'
import iconabout from '../assets/man-user.png'
import iconworks from '../assets/programming.png'
import iconcontact from '../assets/contact.png'

var scrolled = false

export default {
  mounted () {
    window.scrollTo(0, 0)
    setTimeout(() => { this.changeSituation(1) }, 3000)
    setTimeout(() => { this.changeSituation(2) }, 3500)
    setTimeout(() => { this.changeSituation(3) }, 4000)
    setTimeout(() => { this.changeSituation(4) }, 4200)
  },
  data () {
    return {
      showHeader: true,
      aboutStart: false,
      worksStart: false,
      contactStart: false,
      homeStart: false,
      myImage: imageShow,
      myVideo: videoShow,
      iconUser: iconabout,
      iconWorks: iconworks,
      iconContact: iconcontact,
      hovermenu: false,
      mobileMenu: false
    }
  },
  methods: {
    showHomeContent: function () {
      this.$SmoothScroll(document.getElementById('contentHome'), 1500, () => {
        scrolled = false
        this.showHeader = false
        this.homeStart = true
      }, undefined)
      scrolled = true
    },
    handleScroll: function (e) {
      if (!scrolled && this.showHeader) {
        e = window.event || e
        var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)))
        if (delta < 0) {
          this.$SmoothScroll(document.getElementById('contentHome'), 1500, () => {
            scrolled = false
            this.showHeader = false
            this.homeStart = true
            this.playVideo()
          }, undefined)
          scrolled = true
        }
      }
      e.preventDefault()
    },
    changeSituation: function (num) {
      if (num === 1) {
        this.aboutStart = true
      } else if (num === 2) {
        this.worksStart = true
      } else if (num === 3) {
        this.contactStart = true
      } else if (num === 4) {
        this.scrollStart = true
      }
    },
    changeRouter: function (page) {
      this.$parent.changePage(page)
    },
    playVideo: function () {
      // console.log(this.$refs)
      this.$refs.videobg.play()
    },
    menuHover: function () {
      this.hovermenu = true
    },
    menuHoverOut: function () {
      this.hovermenu = false
    },
    videoLoaded: async function () {
      this.$parent.pageLoaded()
    }
  },
  created () {
    window.addEventListener('wheel', this.handleScroll, false)
  },
  destroyed () {
    window.removeEventListener('wheel', this.handleScroll)
  },
  components: {
    'nav-bar': navbar
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  // @import '../styles/main.scss';
</style>



